.TopFooter {
    background-color: #232F3E;
    padding: 0px 20px 20px;
    display: flex;
}

.TopFooter > div {
    display: flex;
    flex-direction: column;
    width: 50%;
}

.TopFooter > div > a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    margin-top: 23px;
}
